* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

/* Nav Bar */
* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  font-family: 'Titillium Web', sans-serif;
  font-size: 20px;
}

.nav {
  height: 50px;
  width: 100%;
  background: #494949;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 1.5rem;
  padding: 5px;
}

.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  font-size: 22px;
  color: #fff;
  padding: 10px 10px 10px 50px;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  float: right;
  font-size: 18px;
  padding-right: 50px;
}

.nav > .nav-links > a {
  display: inline-block;
  padding: 13px 35px 13px 35px;
  text-decoration: none;
  color: #efefef;
  cursor: pointer;
}

.nav > .nav-links > a:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.nav > #nav-check {
  display: none;
}

@media (max-width:900px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }
  .nav > .nav-links {
    position: fixed;
    display: block;
    width: 100%;
    background-color: #333;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
    top: 50px;
    left: 0px;
    padding-right: 0px;
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
    padding: 13px 10px 13px 10px;
    padding-top: 40px;

  }
  .nav > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked ~ .nav-links {
    height: calc(100vh - 50px);
    overflow-y: auto;
  }
  .nav > #nav-check:checked ~ body {
    max-height: 100vh;
    overflow: hidden;

  }
}
/* Header */
#header img{
  max-width: 100%;
  max-height: 100vh;
}


/* body Content */

.bodyContent{
  display: flex;
  max-width: 100%;
  justify-content: space-around;
  align-items: center;

}
@media only screen and (max-width: 900px){
  .bodyContent{
    flex-direction: column;
  }
}
.body-card{
  display: flex;
  flex-direction: column;
  max-height: 500px;
  justify-content: space-around;
  align-items: center;
  margin-top: 50px;
  box-shadow: 1px 5px 12px rgba(61, 61, 61, 0.479);
  border-radius: 20px;
}

.img-container{
  max-height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-container img{
  width: 250px;
  height: 250px;
}
.card-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card-content h3{
  padding-top: 20px;

}
.card-content p{
  padding: 20px;
}

@media (max-width:900px) {
  .bodyContent{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .body-card{
    width: 80%;
    align-items: center;
    justify-content: center;
  }
  .body-card p{
    max-width: 70%;
  }
}

/* Main content under switch */
.switch-content{
  padding-top: 50px;
  min-height: 100vh;
}

.shop-now{
  padding: 10px;
  margin-top: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: white;
  text-decoration: none;
}
.shop-now h1{
  min-width: 90%;
  background:linear-gradient(to bottom left, #EF8D9C 40%, #FFC39E 100%);
  border: 1px solid #EF8D9C;
  text-decoration: none;

}







/* Footer */
/* FOOTER */
.footer {
	width: 100%;
	background: #00121b;
	display: block;
  margin-top: 20px;
 }

 .inner-footer {
	 width: 95%;
	 margin: auto;
	 padding: 30px 10px;
	 display: flex;
	 flex-wrap: wrap;
	 box-sizing: border-box;
	 justify-content: space-around;
 }

.footer-items {
	width: 25%;
	padding: 10px 20px;
	box-sizing: border-box;
	color: #fff;
}

.footer-items p {
	font-size: 16px;
	text-align: justify;
	line-height: 25px;
	color: #fff;
}

.footer-items h1 {
	color: #fff;
}

.border1 {
	height: 3px;
	width: 40px;
	background: #ff9800;
	color: #ff9800;
	background-color: #ff9800;
	border: 0px;
}

ul {
	list-style: none;
	color: #fff;
	font-size: 15px;
	letter-spacing: 0.5px;	
 }

ul a {
	text-decoration: none;
	outline: none;
	color: #fff;
	transition: 0.3s;
}

ul a:hover {
	color: #ff9800;
}

ul li {
	margin: 10px 0;
	height: 25px;
}

li i {
	margin-right: 20px;
}

.social-media {
	width: 100%;
	color: #fff;
	text-align: center;
	font-size: 20px;
}

.social-media a {
	text-decoration: none;
}

.social-media i {
	height: 25px;
	width: 25px;
	margin: 20px 10px;
	padding: 4px;
	color: #fff;
	transition: 0.5s;
}

.social-media i:hover {
	transform: scale(1.5);
}

.footer-bottom {
	padding: 10px;
	background: #00121b;
	color: #fff;
	font-size: 12px;
	text-align: center;
}

/* for tablet mode view */

@media screen and (max-width: 1275px) {
	.footer-items {
		width: 50%;
	}
}

/* for mobile screen view */

@media screen and (max-width: 660px) {
	.footer-items {
		width: 100%;
	}
}

/* you can toggle the media screen view accordingly by changing the (max-width: px) to your convenience */

