.shop-card-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

  .card {
    background-color: white;
    border-radius: 0.25rem;
    box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: space-between;
    align-content: center;
    flex-direction: column;
    overflow: hidden;
    flex-wrap: wrap;
    margin-top: 30px;
    padding-top: 30px;
    width: 30%;
    max-height: 100%;
    min-height: 500px;
  }

  @media only screen and (max-width:900px){
      .shop-card-container{
          flex-direction: column;
      }
      .card{
          width: 80%;
          flex-direction: row;
      }
  }

  .card_image{
    min-height: 60%;
    min-width: 300px;
    max-height: 300px;
    max-width: 100%;
  }

.card-img {
    min-height: 300px;
    min-width: 300px;
    max-height: 300px;
    max-width: 300px;
    vertical-align: middle;
  }
  
  .btn {
    color: #ffffff;
    padding: 0.8rem;
    font-size: 14px;
    text-transform: uppercase;
    border-radius: 4px;
    font-weight: 400;
    display: block;
    width: 100%;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.2);
    background: transparent;
  }
  
  .btn:hover {
    background-color: rgba(255, 255, 255, 0.12);
  }
  

  .cards_item {
    display: flex;
    padding: 1rem;
  }
  
  @media (min-width: 40rem) {
    .cards_item {
      width: 50%;
    }
  }
  
  @media (min-width: 56rem) {
    .cards_item {
      width: 33.3333%;
    }
  }
  
  
  .card_content {
    padding: 1rem;
    background: linear-gradient(to bottom left, #EF8D9C 40%, #FFC39E 100%);
    width: 100%;
    min-height: 50%;

  }
  .card_content h2{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    -webkit-box-orient: vertical;
  }
  
  .card_title {
    color: #ffffff;
    font-size: 1.1rem;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: 0px;
  }
  
  .card_text {
    color: #ffffff;
    font-size: 0.875rem;
    line-height: 1.5;
    margin-bottom: 1.25rem;    
    font-weight: 400;
  }
  #more{
    display:flex;
    justify-content: center;
    align-items: center;
    background:linear-gradient(to bottom left, #EF8D9C 40%, #FFC39E 100%);
    max-height: 100%;
    min-height: 500px;
  }
  #moreLink{
    min-height: 100%;
    font-size: 2rem;
  }

  .more-content{
    background:linear-gradient(to bottom left, #ffffff 40%, #ffffff 100%);
    min-height: 100%;
    max-height: 100%;
  }

  @media only screen and (max-width: 900px){
    .card_content{
      min-height: 0;
    }
  }