.item-landing-page{
    display: flex;
    justify-content: space-between;
    max-width: 100vw;
}

.left{
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    max-width: 50%;
    min-width: 50%;
    box-shadow: 0px -10px 10px rgba(68, 68, 68, 0.459);

}
.item-img-container{
    max-width: 100%;
    max-height: 500px;
    min-height: 550px;
    display: flex;
    justify-content: center;
    align-items: center;

}
.item-img-container img{
    max-width: 100%;
    max-height: 500px;
    transition: 0.4s;
}
.item-img-container:hover img{
    max-height: 550px;
}
.item-buttons{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.item-button{
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-size: 1.3rem;
    width: 47%;
    padding: 10px 10px;
    box-shadow: 0px 2px 8px rgba(31, 31, 31, 0.52);
    border-radius: 10px;
    transition: 0.3s;
}
.item-button:hover{
    letter-spacing: 3px;
}
.add-to-cart{
    background-color: white;
}
.buy-now{
    background-color: rgb(0, 172, 0);
    color: white;
}

.right{
    display: flex;
    flex-direction: column;
    max-width: 45%;
    padding-right: 5%;
    align-items: flex-start;
}
.right-title{
    letter-spacing: 3px;
    font-family: 'Josefin Sans', sans-serif;
    line-height: 30px;
    padding-top: 20px;

}
.branded{
    padding-top: 10px;
    color: rgb(100, 100, 100);
}
.right-description{
    padding-top: 20px;
    padding-bottom: 30px;
}




.price-contaner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 60%;
    color: green;
    font-size: 2rem;

}

.discount{
    padding: 20px;
    color: gray;
    font-size: 1.3rem;
    text-decoration: line-through;
}
.total-discount{
    font-size: 1rem;
}

@media only screen and (max-width: 900px){
    .item-landing-page{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .left{
        max-width: 90%;
        min-width: 90%;
    }
    .right{
        max-width: 90%;
        min-width: 90%;    
    }
    .price-contaner{
        flex-direction: column;
        min-width: 100%
    }
}